function validarEmail(email) {
    // Expresin regular para validar el formato de correo electrnico
    const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    
    // Verificar que el correo electrnico cumpla con los requisitos
    if (!email) {
      return false; // El correo electrnico est vaco
    }
    
    if (email.length > 35) {
      return false; // El correo electrnico tiene ms de 35 caracteres
    }
    
    if (!regex.test(email)) {
      return false; // El correo electrnico no cumple con el formato vlido
    }
    
    return true; // El correo electrnico es vlido
  }
  
 

  function validarPassword(password) {
    // Expresin regular para validar la contrasea
    const regex = /^(?=.*[0-9]).{6,10}$/;
    
    // Verificar que la contrasea cumpla con los requisitos
    if (!regex.test(password)) {
      return false; // La contrasea no cumple con los requisitos
    }
    
    return true; // La contrasea es vlida
  }
module.exports = {
    validarEmail:validarEmail,
    validarPassword:validarPassword
}